*{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
.mission-sect{
    width: 100%;
    height: 100%;
}
.mission{
    width: calc(100% - 70%);
    margin: 2vh auto;
    height: 60vh;
    border-radius: 50%;
    box-shadow: 0 1vh 0.8vh #0a50a1, 0 1vh 4vh #0a50a1;
    background-color: rgb(237, 242, 243);
}
.mission-title{
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    letter-spacing: 0.5vh;

}
.mission-title img{
    width: 100%;
    height: 35%;
    margin-top: 4vh;
}
.mission-title h3{
    text-align: center;
    font-size: 8vh;
    font-weight: 500;
}
.mission-desc{
    width: calc(100% - 10%);
    height: 100%;
    display: grid;
    grid-template-columns: repeat(3,0.9fr);
    margin: 4vh auto;
    border-radius: 1vh;
    padding: 4vh;
    box-shadow: 0 1vw 1vw rgba(0,0,0,0.12), 0 0.1vw 0.2vw rgba(0,0,0,0.24);
    line-height: 6vh;
    font-weight: 500;
    letter-spacing: 0.3vh;
}
.mission-desc h4{
    text-align: left;
    font-size: 5vh;
    color: #666;
    margin: 1vh 0 2vh 0;
}
.mission-desc h4 i{
    color: #0a50a1;
}
.mission-desc p{
    text-align: left;
    width: 90%;
    font-size: 2.2vh;
}

.trusted-section{
    width: calc(100% - 40%);
    margin: 15vh auto;
    box-shadow: 0 1vw 1vw rgba(0,0,0,0.12), 0 0.1vw 0.2vw rgba(0,0,0,0.24);
}
.trusted-section h2{
    font-size: 6vh;
    text-align: center;
    border-bottom: 3px groove #666;
    margin: 0 30% 5vh;
    font-weight: 500;
    letter-spacing: 0.5vh;
}
/* -------------- */
.compSlider{
    position: relative;
    height: 40vh;
    display: grid;
    justify-content: center;
}
.compSlider img{
    width: 60vh;
	position: relative;
    border-radius: 1vh;
    height: 30vh;
    object-fit: contain;
}
.comp{
    opacity: 0;
    transition-duration: 1s ease;
}
.comp.active{
    opacity: 1;
    transition-duration: 1s;
    transform: scale(1.08);
}
/* Mobile Styling */
.mission-sect.mob .mission{
    width: 95%;
    height: 50vh;
}
.mission-sect.mob .mission-desc{
    grid-template-columns: none;
    letter-spacing: 0.2vh;
}

.mission-sect.mob .mission-desc h4{
    font-size: 2em;
}
.mission-sect.mob .mission-desc p{
    width: 100%;
}

.compSlider.mob img{
    width: 90%;
    margin: 0 5%;
}
.trusted-section.mob{
    width: 90%;
}
.trusted-section.mob h2{
    margin: 0 5%;
}