footer{
	position: relative;
	bottom: 0;
}
.footer-distributed{
	background-color: #0a50a1;
	box-sizing: border-box;
	width: 100%;
	height: 35vh;
	text-align: left;
	font: bold 16px sans-serif;
	padding: 2vh 0 0 5vh;
}
.footer-distributed .footer-left,
.footer-distributed .footer-center,
.footer-distributed .footer-right{
	display: inline-block;
	vertical-align: top;
}

/* Footer left */

.footer-distributed .footer-left{
	width: 30%;
	line-height: 6vh;
}
/* The company logo */

.footer-distributed .footer-left img{
	width: 60%;
}
.footer-distributed h3 span{
	color:  #e0ac1c;
}
/* Footer links */

.footer-distributed .footer-links{
	color:  #ffffff;
	margin: 20px 1vh 12px;
}
.footer-distributed .footer-links a{
	display:inline-block;
	line-height: 1.8vh;
	text-decoration: none;
	color:  inherit;
}
.footer-distributed .footer-company-name{
	color:  #8f9296;
	font-size: 14px;
	font-weight: normal;
	margin: 0;
}
/* Footer Center */
.footer-distributed .footer-center{
	width: 35%;
	line-height: 6vh;

}
.footer-distributed .footer-center i{
	color: #ffffff;
	font-size: 25px;
	width: 38px;
	height: 38px;
	border-radius: 50%;
	text-align: center;
	line-height: 42px;
	margin: 10px 15px;
	vertical-align: middle;
}
.footer-distributed .footer-center i.fa-envelope{
	font-size: 17px;
	line-height: 38px;
}
.footer-distributed .footer-center p{
	display: inline-block;
	color: #ffffff;
	vertical-align: middle;
	margin:0;
}
.footer-distributed .footer-center p span{
	display:block;
	font-weight: normal;
	font-size:14px;
	line-height:2;
}
.footer-distributed .footer-center p a{
	color:  White;
	text-decoration: none;;
}
/* Footer Right */

.footer-distributed .footer-right{
	width: 30%;

}
.footer-distributed .footer-company-about{
	line-height: 6vh;
	color:  #ffffff;
	font-size: 1.1em;
	font-weight: normal;
	margin: 0;
}
.footer-distributed .footer-company-about span{
	display: block;
	color:  #ffffff;
	font-size: 1.5em;
	font-weight: bold;
}
.footer-distributed .footer-icons{
	margin-top: 25px;
}
.footer-distributed .footer-icons a{
	display: inline-block;
	width: 35px;
	height: 35px;
	cursor: pointer;
	border-radius: 2px;

	font-size: 30px;
	color: #ffffff;
	text-align: center;
	line-height: 35px;

	margin: 0 15px;
	background: transparent;
}
/* Mobile Styling */
.footer-distributed.mob{
	height: 100%;
	position: static;
}
.footer-distributed.mob .footer-left,
.footer-distributed.mob .footer-center,
.footer-distributed.mob .footer-right{
	display: block;
	width: 90%;
	text-align: center;
}

.footer-distributed.mob .footer-center i{
	margin-left: 0;
}
