.contact-section{
    background: url(../assets/introimg.JPG) no-repeat ;
    background-size: cover;
    padding: 10vh 0;
}
.contact-section h1{
    text-align: center;
    color: rgb(255, 255, 255);
    font-size: 5vh;
}
.contact-border{
    width: 10vh;
    height: 1vh;
    background: #34495e;
    margin: 4vh auto;
}
.contact-form{
    max-width: 60vh;
    margin: auto;
    padding: 0 1vh;
    overflow: hidden;
}
.contact-form [type=text],[type=email],
.contact-form textarea
{
    display: block;
    width: 100%;
    box-sizing: border-box;
    margin: 2vh 0;
    border:0;
    background: #fff;
    padding: 2vh 4vh;
    outline: none;
    color: rgb(0, 0, 0);
    transition: 0.5s;
}
.contact-form [type=text]:focus,[type=email]:focus,
.contact-form textarea:focus{
    box-shadow: 0 0 1vh 0.5vh #0a50a1;
}
.contact-form textarea{
    resize: none;
    height: 12vh;
}
.contact-form-btn{
    float: right;
    background: #34495e;
    color: #fff;
    padding: 1vh 5vh;
    border-radius: 2vh;
    cursor: pointer;
    transition: 0.5s;
    border: none;
}
.contact-form-btn:hover{
    background: #0a50a1;
}