.products-body h2{
    position: relative;
    font-size: 8vh;
    letter-spacing: 1vh;
    margin: 0 30% 0 30%;
    transform: translate(0, 25%);
    border-bottom: 3px groove rgb(121, 121, 121);
    color: #0a50a1;
    user-select: none;

}
.container-id-cards{
    display: grid;
    grid-template-columns: repeat(3, 0fr);
    margin-top: 15vh;
}
.cards-id-home{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 25vw;
    height: 50vh;
    padding: 2vh 2vw;
    margin: 8vh;
    background: #fff;
    position: relative;
    box-shadow: 0 .1vh .3vh rgba(0,0,0,0.12), 0 .1vh .2vh rgba(0,0,0,0.24);
    transition: all 1s cubic-bezier(.25,.8,.25,1);
    line-height: 3.5vh;
    user-select: none;
    cursor: pointer;
}
.cards-id-home div:nth-child(2){
    flex: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.cards-id-home div:nth-child(3){
    flex: 1;
    display: flex;
    justify-content: center;
    width: 25vw;
}
.cards-id-home img{
    width: 90%;
    height: 95%;
    transition: all 0.2s ease-in-out;

}
.cards-id-home h1{
    font-size: 4vh;
    color: #444;
    text-align: center;
}
.cards-id-home p{
    display: none;
    font-size: 3vh;
}
.cards-id-home:hover{
    box-shadow: 0 1.4vh 2.8vh rgba(0,0,0,0.25), 0 1vh 1vh rgba(0,0,0,0.22);
    transform: translateY(-3%);
    height: 80vh;
}
.cards-id-home:hover img
{
    display: none;
}
.cards-id-home:hover p{
    display: block;
}
.cards-id-home:hover h1{
    color: #0a50a1;
}
.cards-id-home:hover p{
    color: black;
}
/* Mobile Styling */
.products-body.mob h2{
    text-align: center;
    font-size: 2.2rem;
    margin: 0 2%;
    transform: translate(0,50%);
}
.products-body.mob .container-id-cards{
    grid-template-columns: none;
    justify-content: center;
    margin-top: 0vh;
}
.products-body.mob .cards-id-home{
    width: 90vw;
    height: 70vh;
    padding: 1vh 1vw;
}
.products-body.mob .cards-id-home img{
    width: 100%;
}