.carouselSlider{
    position: relative;
	height: 95vh;
    width: 100%;
    margin-bottom: 15vh;
    padding: 1vh 0;
	overflow: hidden;
}
.carouselSlider img{
	object-fit: center;
    width: 96vw;
    height: 100vh;
	position: relative;
	left: 0%;
    border-radius: 1vh;
}
.right-arrow{
    position: absolute;
    top: 50%;
    right: 1vh;
    font-size: 2.2vw;
    z-index: 1;
    cursor: pointer;
    user-select: none;
	color: white;
}
.left-arrow{
    position: absolute;
    top: 50%;
    left: 0vh;
    font-size: 2.2vw;
    z-index: 1;
    cursor: pointer;
    user-select: none;
	color: white;

}
.caSlider{
    opacity: 0;
    transition-duration: 1s ease;
}
.caSlider.active{
    opacity: 1;
    transition-duration: 1s;
    transform: scale(1.08);
}
.right-arrow:hover,
.left-arrow:hover
{
color: #1b9bff;
}