.about-bg{
	width: 100%;
	height: 100vh;
	background: url(../assets/skf1.JPG) no-repeat center ;
	background-size: 95%;
}
.about-cont {
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 100px 0;
  background: url(../assets/skf4.JPG);
}

.timeline {
  width: 80%;
  height: auto;
  max-width: 47vw;
  margin: 0 auto;
  position: relative;
}
.timeline ul {
  list-style: none;
}
.timeline ul li {
	position: relative;
	width: 100%;
	height: 100%;
	padding: 20px;
  	background-color: #1e1f22;
  	color: white;
  	border-radius: 10px;
	  margin: 1vh 0;
}
.timeline ul li:last-child {
  margin-bottom: 0;
}
.timeline-content{
	display: flex;
	flex-direction: column;
	align-items: center;
}

.timeline-content p {
  font-size: 1.5rem;
  font-weight: 300;
  letter-spacing: .2rem;
}
.timeline-content p span{
  font-weight: 500;
}
.timeline:before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 2px;
  height: 100%;
  background-color: gray;
}
.timeline ul li:nth-child(odd) {
  float: left;
  clear: right;
  transform: translateX(-480px);
  border-radius: 20px 0px 20px 20px;
}
.timeline ul img{
  width: 100%;
  height: 35vh;
}
.timeline ul li:nth-child(even) {
  float: right;
  clear: left;
  transform: translateX(480px);
  border-radius: 0px 20px 20px 20px;
}
.timeline ul li::before {
  content: "";
  position: absolute;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background-color: gray;
  top: 0px;
}
.timeline ul li:nth-child(odd)::before {
  transform: translate(50%, -50%);
  right: -30px;
}
.timeline ul li:nth-child(even)::before {
  transform: translate(-50%, -50%);
  left: -30px;
}
.timeline-content .title {
  font-size: 5vh;
  line-height: 10vh;
  letter-spacing: 0.5vh;
}
.timeline ul li:hover::before {
  background-color: #1b9bff;
}
/* Mobile Styling */
.about-bg.mob{
  background-size:100% 100%;
}
.about-cont.mob{
  background: none no-repeat content-box;
}
.about-cont.mob .timeline{
  width: 95%;
  max-width: 100vw;
  margin: 0;
}
.about-cont.mob .timeline ul li{
  float: none;
  transform: translate(0);
  padding: 2vh;
}
.about-cont.mob .timeline ul img{

  height: 25vh;
}
.about-cont.mob .timeline:before, .about-cont.mob .timeline ul li::before{
  content: none;
}
.about-cont.mob .timeline-content .title{
  font-size: 1.8rem;
}
.about-cont.mob .timeline-content p {
  font-size: 1.1rem;
  letter-spacing: 0.1em;
}