*{
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
    box-sizing: border-box;
}
.appbar{
    background-color: #0a50a1;
    height: 8.1vh;
}
.appbar.logo{
    cursor: pointer;
}
.appbar.logo img{
    width: 15vw;
    height: 7vh;
    margin: 0.5vh 0;
}
.appbar ul{
    float: right;
    margin-right: 2vh;
}
.appbar ul li{
    display: inline-block;
    line-height: 8vh;
    margin: 0 1vh;
}
.appbar ul li a{
    color: white;
    font-size: 2vh;
    padding: 1vh 1.3vh;
    border-radius: .5vh;
    text-transform: uppercase;
}
.appbar ul li a.active,a:hover{
    background: #1b9bff;
    transition: .5s;
}
.checkappbar-btn{
    font-size: 3vh;
    color: white;
    float: right;
    line-height: 8vh;
    margin-right: 4vh;
    cursor: pointer;
    display: none;
}
#checkappbar{
    display: none;
}
/* Mobile Styling */

.appbar.mob .logo{
    display: none;
}
.appbar.mob .logo img{
    width: 30vw;
    padding: 0;
    background-color: black;
}
.appbar.mob ul{
    margin: 0;
    padding: 0 2vh;
}

.appbar.mob ul li a{
    margin: 0 .5vw;
    padding: 0;
}